          </g></svg>
          <p>Watch later</p>
        </div>
        <div class="category-item" :class="{'active': fixedNav(5)}">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M3.75 18.75h3v-9h-3v9zm16.5-8.25c0-.83-.68-1.5-1.5-1.5h-4.73l.7-3.43.03-.24c0-.3-.13-.6-.33-.8l-.8-.78L8.7 8.7c-.3.26-.45.64-.45 1.05v7.5c0 .82.67 1.5 1.5 1.5h6.75c.62 0 1.15-.38 1.38-.9l2.27-5.3c.06-.18.1-.36.1-.55v-1.5z" class="style-scope yt-icon"></path>
          </g></svg>
          <p>Liked videos</p>
        </div>
        <p class="part">SUBSCRIPTIONS</p>
        <div class="category-item">
          <div class="sub" style="background: url('../../static/image/photo.jpg');"></div>
          <p>Jason Chen</p>
        </div>
        <p class="part"></p>
        <p>&copy;2018 MyTube, LLC</p>
      </div>
    </div>
    <div v-if="loaded" class="history-content">
      <div v-for="(item, index) in historyData" class="history-item">
        <div class="pic" :style="renderPic(index)"></div>
        <p>{{renderText(item.video_src)}}</p>
        <p>{{item.author}}</p>
        <p>{{renderViews(item.views)}}</p>
      </div>
      <div v-show="pc" class="history-search">
        <div class="searchBox">
          <input type="text" placeholder="Search watch history" @click="search" @blur="cancelSearch">
          <div class="icon">
            <div></div>
          </div>
          <div class="reset">
            <div></div>
          </div>
          <div class="input-bottom">
            <div class="old"></div>
            <transition name="type">
              <div v-show="searchStatus" class="new"></div>
            </transition>
          </div>
        </div>
        <div class="searchModeName">
          <p style="font-size: 16px; margin-top: 16px;">History type</p>
        </div>
        <div v-for="item in historyMode" class="searchMode">
          <p style="font-size: 14px;">{{item}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import {checkCookie} from '../js/cache.js'
  export default {
    name: 'history',
    data () {
      return {
        fullStatus: false,
        showBar: true,
        index2: 3,
        pc: true,
        searchStatus: false,
        historyMode: [
          'Watch history',
          'Search history',
          'Comments',
          'Community'
        ],
        historyData: [],
        loaded: false
      }
    },
    methods: {
      fixedNav: function (i) {
        if (i === this.index2) {
          return true
        } else {
          return false
        }
      },
      home: function () {
        this.$router.push({path: '/home'})
      },
      history: function () {
        this.$router.push({path: '/history'})
      },
      search: function () {
        this.searchStatus = true
      },
      cancelSearch: function () {
        this.searchStatus = false
      },
      renderPic: function (i) {
        var str = 'background-size: cover;'
        var str2 = 'background-image: url(\'static/image/' + this.historyData[i].image_src + '\');'
        return str + str2
      },
      renderText: function (x) {
        var temp = x
        var reg = new RegExp('-', 'g')
        return temp.substring(0, temp.length - 4).replace(reg, ' ')
      },
      renderViews: function (x) {
        var temp = x
        if (temp > 1000000) {
          temp = (temp - temp % 1000000) / 1000000
          temp += 'M views'
        } else {
          temp = (temp - temp % 1000) / 1000
          temp += 'K views'
        }
        return temp
      }
    },
    beforeCreate () {
      var self = this
      $.getJSON('show_history.php', function (data) {
        self.historyData = data
        console.log(self.historyData)
        // for (var i = data.length - 1; i >= 0; i--) {
        //   ss = ss + data[i].author + data[i].video_src + data[i].image_src + data[i].gif_src + data[i].views
        // }
      })
    },
    mounted () {
      var self = this
      this.$root.eventHub.$emit('nav', 5)
      this.$root.eventHub.$on('bar3', function () {
        self.showBar = !self.showBar
      })
      if (!checkCookie(2, '')) {
        self.loaded = false
        this.$root.eventHub.$emit('prompt')
        this.$router.push({path: '/sign'})
      } else {
        self.$root.eventHub.$emit('loading')
        setTimeout(function () {
          self.loaded = true
          self.$root.eventHub.$emit('load')
        }, 1000)
      }
      if (window.screen.width > 768) {
        this.pc = true
      } else {
        this.pc = false
      }
    }
  }
</script>

<style scoped>
  @media (max-device-width: 767px) {
    .history{
      width: 100%;
      height: auto;
      padding: 30px 0;
      position: absolute;
      top: 130px;
      background: white;
    }
    .history-item{
      width: 100%;
      height: 270px;
      position: relative;
      overflow: hidden;
    }
    .history-item .pic{
      width: 432px;
      height: 243px;
      border-radius: 2px;
      margin: 12px 0 40px 40px;
      cursor: pointer;
      float: left;
    }
    .history-item:not(:first-of-type) .pic{
      margin-top: 16px;
    }
    .history-item p{
      width: calc(100% - 500px);
      padding: 16px 10px 0;
      float: right;
    }
    .history-item p:first-of-type{
      font-size: 36px;
      line-height: 120%;
      height: auto;
      color: #202020;
      text-shadow: 0 0 1px #C8C8C8;
      top: 0;
    }
    .history-item p:not(:first-of-type){
      color: #646464;
    }
    .history-item p:nth-of-type(2){
      font-size: 34px;
      top: 110px;
    }
    .history-item p:nth-of-type(3){
      font-size: 34px;
      top: 160px;
    }
  }

  @media (min-device-width: 768px) {
    .history{
      width: 100%;
      height: auto;
      min-height: calc(100% - 58px);
      margin: 58px 0 0;
      overflow: hidden;
      background: #FAFAFA;
    }
    #category{
      position: fixed;
      display: none;
    }
    .category-item{
      width: calc(100% - 48px);
      height: 40px;
      padding: 1px 24px 0;
      overflow: hidden;
      cursor: pointer;
    }
    #category .category-item:hover{
      background: #EBEBEB;
    }
    .category-item:first-of-type{
      margin-top: 10px;
    }
    .category-item:nth-of-type(2), .category-item:nth-of-type(4){
      margin-top: 0;
      margin-bottom: 0;
    }
    .category-item:nth-of-type(3), .category-item:nth-of-type(6){
      margin-bottom: 10px;
    }
    .category-item svg{
      width: 24px!important;
      height: 24px!important;
      fill: #888888;
      padding: 8px 26px 8px 0;
      float: left;
    }
    .category-item.active svg{
      fill: #1296db;
    }
    .category-item p{
      font-size: 15px;
      line-height: 40px;
      color: #444444;
      float: left;
    }
    .part{
      width: calc(100% - 48px);
