      margin: 0 40px;
      padding: 3px 10px 2px;
      outline: none;
      border: none;
      font-size: 14px;
      background: #FAFAFA;
    }
    .searchBox .input-bottom{
      width: calc(100% - 20px);
      height: 4px;
      overflow: hidden;
      margin: 0 10px;
      position: relative;
    }
    .input-bottom .old{
      width: 100%;
      height: 1px;
      position: absolute;
      top: 2px;
      background: #C0C0C0;
    }
    .input-bottom .new{
      width: 100%;
      height: 2px;
      position: absolute;
      top: 1px;
      background: #1F1F1F;
    }
    .type-enter-active{
      -webkit-animation: enter .3s;
      -moz-animation: enter .3s;
      -o-animation: enter .3s;
      animation: enter .3s;
    }
    .type-leave-active{
      -webkit-animation: leave .3s;
      -moz-animation: leave .3s;
      -o-animation: leave .3s;
      animation: leave .3s;
    }
    @-webkit-keyframes enter {
      0%{
        -webkit-transform: translateX(100%);
      }
      100%{
        -webkit-transform: translateX(0);
      }
    }
    @-moz-keyframes enter {
      0%{
        -moz-transform: translateX(100%);
      }
      100%{
        -moz-transform: translateX(0);
      }
    }
    @-o-keyframes enter {
      0%{
        -o-transform: translateX(100%);
      }
      100%{
        -o-transform: translateX(0);
      }
    }
    @keyframes enter {
      0%{
        transform: translateX(100%);
      }
      100%{
