      margin-left: calc((100% - 1070px) / 2);
    }
    .history.full .history-search{
      right: calc((100% - 1284px) / 2);
    }
    .history .history-search{
      right: calc((100% - 240px - 1070px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1604px) {
    .history.full .history-item, .history .history-item{
      width: 899px;
      margin-left: calc((100% - 1284px) / 2);
    }
    .history.full .history-search{
      right: calc((100% - 1284px) / 2);
    }
    .history .history-search{
      right: calc((100% - 240px - 1284px) / 2);
    }
  }
</style>



