<script>
  import $ from 'jquery'
  import {checkCookie} from '../js/cache.js'
  import lrz from '../../node_modules/lrz/dist/lrz.all.bundle'
  export default {
    name: 'center',
    data () {
      return {
        showBar: true,
        user: '',
        setHead: false,
        pc: true,
        loaded: false,
        editStatus: false,
        success: false
      }
    },
    methods: {
      setPic: function () {
        $('#pic-setter').click()
      },
      history: function () {
        this.$router.push({path: '/history'})
        if (window.screen.width > 768) {
          this.$root.eventHub.$emit('full', true)
        } else {
          this.$root.eventHub.$emit('nav', 5)
        }
      },
      home: function () {
        this.$router.push({path: '/home'})
      },
      creator: function () {
        this.$router.push({path: '/creator'})
      },
      clear: function () {
        this.home()
        this.$root.eventHub.$emit('clear')
      },
      upload: function (e) {
        var files = e.target.files || e.dataTransfer.files
        var self = this
        if (!files.length) {
          return
        }
        // eslint-disable-next-line
        lrz(files[0], {
          height: 200
        })
          .then(function (rst) {
            var sourceSize = files[0].size / 1024
            var resultSize = rst.fileLen / 1024
            var scale = parseInt(100 - (resultSize / sourceSize * 100))
            alert('Reduce Size Over ' + scale + '%')
            self.setHead = true
            self.user = rst.base64
            self.$root.head = rst.base64
            $.ajax({
              url: 'user_update.php',
              type: 'POST',
              data: {base64: rst.base64},
              success: function (data) {
                console.log(data)
              }
            })
            $('#user-pic').css('background-image', 'url(\'' + self.user + '\')')
            return rst
          })
          .catch(function (err) {
            // 万一出错了，这里可以捕捉到错误信息
            // 而且以上的then都不会执行
            alert('上传失败！请尝试再次上传！')
            console.log(err)
          })
          .always(function () {
            e.target.value = null
          })
      },
      edit: function () {
        this.editStatus = !this.editStatus
      },
      editSubmit: function () {
        var self = this
        $.ajax({
          url: 'user_update.php',
          type: 'POST',
          data: {nickname: $('#nickname').val(), gender: $('#gender').val(), tel_email: $('#tel').val()},
          success: function (data) {
            if (data === 'update data successfully') {
              self.success = true
              setTimeout(function () {
                self.success = false
                self.editStatus = false
              }, 2000)
            }
          }
        })
      }
    },
    computed: {
      example: function () {
        if (window.screen.width >= 768) {
          return 10
        } else {
          return 4
        }
      },
      navText: function () {
        if (this.editStatus) {
          return 'Edit'
        } else {
          return 'Home'
        }
      },
      userHead: function () {
        if (this.$root.head === '') {
          return 'background: url(\'static/assets/user-circle3.jpg\')'
        } else {
          return 'background: url(\'' + this.$root.head + '\')'
        }
      }
    },
    mounted () {
      var self = this
      if (window.screen.width >= 768) {
        this.showBar = true
        this.pc = true
        if (!checkCookie(2, '')) {
          this.$root.eventHub.$emit('prompt', 'center')
          this.$router.push({path: '/sign'})
        } else {
          self.$root.eventHub.$emit('loading')
          setTimeout(function () {
            self.loaded = true
            self.$root.eventHub.$emit('load')
          }, 1000)
        }
      } else {
        this.showBar = false
        this.pc = false
        this.loaded = true
        this.$root.eventHub.$emit('nav', 4)
      }
      this.$root.eventHub.$on('bar2', function () {
        self.showBar = !self.showBar
        self.$root.eventHub.$emit('full', self.showBar)
      })
    }
  }
</script>

