    .edit-page table{
      width: 100%;
      height: 100%;
      text-align: center;
    }
    .edit-page table tr td{
      height: 48px;
    }
    .edit-page table tr td:first-of-type{
      width: 16%;
      font-size: 16px;
      color: #555555;
    }
    .edit-page table tr:not(:last-of-type) td:last-of-type input{
      width: 80%;
      height: 32px;
      border: none;
      outline: none;
      border-bottom: 1px solid #c8c8c8;
      text-align: center;
      font-size: 16px;
    }
    .edit-page table tr:not(:last-of-type) td:last-of-type input:focus{
      border-bottom: 1px solid #1296db;
    }
    .edit-page table tr:last-of-type input{
      width: 150px;
      height: 36px;
      background: #1296db;
      border-radius: 6px;
      color: white;
      outline: none;
      border: none;
      margin-top: 30px;
      cursor: pointer;
    }
    .edit-page table tr:last-of-type input:active{
      box-shadow: 0 3px 12px grey;
      background: #1E90FF;
    }
    #success-img{
      width: 210px;
      margin: calc((100% - 210px) / 2 + 200px) calc((100% - 210px) / 2) 16px;
      -webkit-animation: up .4s;
      -moz-animation: up .4s;
      -o-animation: up .4s;
      animation: up .4s;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }

    @-webkit-keyframes up {
      0%{
        opacity: 0;
        -webkit-transform: translateY(80px);
      }
      100%{
        opacity: 1;
        -webkit-transform: translateY(0);
      }
    }
    @-moz-keyframes up {
      0%{
        opacity: 0;
        -moz-transform: translateY(80px);
      }
      100%{
        opacity: 1;
        -moz-transform: translateY(0);
      }
    }
    @-o-keyframes up {
      0%{
        opacity: 0;
        -o-transform: translateY(80px);
      }
      100%{
        opacity: 1;
        -o-transform: translateY(0);
      }
    }
    @keyframes up {
      0%{
        opacity: 0;
        transform: translateY(80px);
      }
      100%{
        opacity: 1;
        transform: translateY(0);
      }
    }
    .text1-enter-active{
      -webkit-animation: show .6s;
      -moz-animation: show .6s;
      -o-animation: show .6s;
      animation: show .6s;
    }
    .text2-enter-active{
      -webkit-animation: show2 .8s;
      -moz-animation: show2 .8s;
      -o-animation: show2 .8s;
      animation: show2 .8s;
    }
    @-webkit-keyframes show {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @-moz-keyframes show {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @-o-keyframes show {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @keyframes show {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @-webkit-keyframes show2 {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @-moz-keyframes show2 {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @-o-keyframes show2 {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    @keyframes show2 {
      0%, 66.6%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
  }

  @media (min-device-width: 768px) and (min-width: 600px) {
    .subscription{
      width: 33.3%;
    }
    .button{
      position: relative;
      top: 25px;
      right: 30px;
      float: right;
    }
  }

  @media (min-device-width: 768px) and (min-width: 780px) {
    .user-header .pic{
      margin: 13px 0 47px 67px;
    }
    .button{
      top: 34px!important;
      right: 50px;
      clear: none;
    }
    .subscriptions{
      width: calc(100% - 120px);
      margin: 0 60px;
    }
  }

  @media (min-device-width: 768px) and (min-width: 860px) {
    .user-header .pic{
      margin: 13px 0 47px 107px;
    }
    .button{
      right: 100px;
    }
    .subscriptions{
      width: calc(100% - 200px);
      margin: 0 100px;
    }
  }

  @media (min-device-width: 768px) and (min-width: 980px) {
    .subscription{
      width: 25%;
    }
  }

  @media (min-device-width: 768px) and (min-width: 1160px) {
    .subscription{
      width: 20%;
    }
  }

  @media (min-device-width: 768px) and (min-width: 1294px) {
    #category-content{
      width: 100%;
      height: 1000px;
    }
    #category{
      display: inline;
      width: 240px;
      height: calc(100% - 58px);
      background: #F5F5F5;
      overflow-y: scroll;
    }
    .center-content.full{
      width: calc(100% - 240px);
      margin-left: 240px;
    }
    .center-content.full .subscription{
      width: 25%;
    }
    .center-content .subscription{
      width: 20%;
    }
  }

  @media (min-device-width: 768px) and (min-width: 1400px) {
    .center-content.full .subscription{
      width: 20%;
    }
    .center-content .subscription{
      width: 16.66%;
    }
  }

  @media (min-device-width: 768px) and (min-width: 1640px) {
    .center-content.full .subscription{
      width: 16.66%;
    }
    .center-content .subscription{
      width: 14.28%;
    }
  }

  @media (min-device-width: 768px) and (min-width: 1760px){
    .center-content.full .subscription{
      width: 14.28%;
    }
    .center-content .subscription{
      width: 12.5%;
    }
  }
</style>



