<template>
  <div id="hot">
    <div class="mobile-content">
      <div v-for="i in 10" @click="video">
        <div class="video-pic" :style="picture(2, i)"></div>
        <div class="video-blogger" style="background: url('static/assets/2.png')"></div>
        <div class="video-intro">
          <p class="video-intro-title">Try Not to Laugh Watching Logan Paul Top Vines Compilation w/ Titles 2016</p>
          <p class="video-intro-blogger">Vine Mansion</p>
          <p class="video-intro-views">15M views</p>
          <p class="video-intro-time">1 year ago</p>
        </div>
        <div class="watch-later" @click="watchLater"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'hot',
    methods: {
      watchLater: function () {
        alert('watch later')
      },
      video: function () {
        this.$router.push({path: '/video'})
      },
      picture: function (a, b) {
        var temp = a * 6 + b
        var str = 'background: url(\'static/youtube_image/' + temp + '.jpg\') 50%;'
        var str2 = 'background-size: cover;'
        return str + str2
      }
    },
    mounted () {
      this.$root.eventHub.$emit('nav', 1)
    }
  }
</script>

<style scoped>
  #hot{
    width: 100%;
    height: auto;
    overflow-x: hidden;
  }
  .mobile-content{
    width: 100%;
    height: auto;
  }
  .mobile-content>div{
    width: calc(100% - 72px);
    height: 740px;
    overflow: hidden;
    margin: 0 36px;
  }
  .mobile-content>div:not(:last-of-type){
    border-bottom: 3px solid #f0f2f5;
  }
  .video-pic{
    width: 100%;
    height: 510px;
    margin-top: 30px;
  }
  .video-blogger{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 25px 25px 75px 0;
    float: left;
  }
  .video-pic, .video-blogger{
    background-size: cover!important;
    background-position: 50%!important;
  }
  .video-intro{
    width: calc(100% - 185px);
    height: 200px;
    float: left;
    overflow: hidden;
  }
  .video-intro-title{
    font-size: 36px;
    line-height: 140%;
    margin-top: 25px;
  }
  .video-intro-blogger, .video-intro-views, .video-intro-time{
    float: left;
    font-size: 32px;
    margin: 10px 5px;
  }
  .video-intro-blogger:after, .video-intro-views:after{
    content: " ·";
  }
  .watch-later{
    width: 60px;
    height: 125px;
    background: url("../../static/assets/setting.png") 50% 36px no-repeat;
    background-size: 80%;
    float: left;
  }
  ::-webkit-scrollbar{
    display: none;
  }
</style>



