<template>
  <div id="category">
    <div class="mobile-content">
      <div v-for="(item, index) in category">
        <img class="logo" :class="item.style" :src="item.img" alt="logo">
        <p>{{item.name}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'category',
    data () {
      return {
        category: [
          {name: 'Breaking', img: 'static/assets/newspaper.png', style: 'breaking'},
          {name: 'Movies', img: 'static/assets/movie.png', style: 'movie'},
          {name: 'Lifestyle', img: 'static/assets/lifestyle.png', style: 'lifestyle'},
          {name: 'Sports', img: 'static/assets/sport.png', style: 'sport'},
          {name: 'Amusement', img: 'static/assets/amusement.png', style: 'amusement'},
          {name: 'Ad.', img: 'static/assets/ad.png', style: 'ad'},
          {name: 'Dance', img: 'static/assets/dance.png', style: 'dance'},
          {name: 'Technology', img: 'static/assets/technology.png', style: 'technology'},
          {name: 'Science', img: 'static/assets/science.png', style: 'science'}
        ]
      }
    },
    mounted () {
      this.$root.eventHub.$emit('nav', 2)
    }
  }
</script>

<style scoped>
  #category{
    width: 100%;
    height: auto;
    overflow-x: hidden;
  }
  .mobile-content{
    width: calc(100% - 72px);
    height: auto;
    overflow: hidden;
    margin: 40px 36px;
  }
  .mobile-content>div{
    width: 33.33%;
    height: 250px;
    float: left;
    margin-top: 5px;
  }
  .logo{
    width: 40%;
    margin: 10px 30% 20px;
  }
  .breaking{
    width: 121px;
    height: 132px;
    margin: 20px calc((100% - 121px) / 2) 6px;
  }
  .movie{
    width: 121px;
    height: 105px;
    margin: 29px calc((100% - 121px) / 2) 24px;
  }
  .lifestyle{
    width: 121px;
    height: 120px;
    margin: 20px calc((100% - 121px) / 2) 18px;
  }
  .sport{
    width: 146px;
    height: 121px;
    margin: 20px calc((100% - 146px) / 2) 26px;
  }
  .amusement{
    width: 134px;
    height: 134px;
    margin: 14px calc((100% - 134px) / 2) 18px;
  }
  .ad{
    width: 117px;
    height: 117px;
    margin: 21px calc((100% - 120px) / 2) 28px;
  }
  .dance{
    width: 146px;
    height: 170px;
    margin: 6px calc((100% - 146px) / 2) 0;
  }
  .technology{
    width: 118px;
    height: 118px;
    margin: 36px calc((100% - 118px) / 2) 22px;
  }
  .science{
    width: 144px;
    height: 144px;
    margin: 22px calc((100% - 144px) / 2) 10px;
  }
  .mobile-content>div p{
    font-size: 36px;
    color: #646464;
    width: 100%;
    text-align: center;
  }
  ::-webkit-scrollbar{
    display: none;
  }
</style>



