<script>
  import {checkCookie} from '../js/cache'
  import $ from 'jquery'
  export default {
    name: 'sign',
    data () {
      return {
        inputStatus: false,
        inputStatus3: false,
        inputData: false,
        error: false,
        error2: false,
        error3: false,
        page: 0,
        prompt: '',
        result: 2,
        button: 'sign in',
        check: false,
        signMode: '',
        account: '',
        password: ''
      }
    },
    methods: {
      input: function () {
        this.inputStatus = true
      },
      input3: function () {
        this.inputStatus3 = true
      },
      leave: function () {
        this.inputStatus = false
        if (event.target.value === '') {
          this.inputData = false
        }
      },
      leave3: function () {
        this.inputStatus3 = false
      },
      type: function () {
        if (event.target.value !== '') {
          this.inputData = true
        } else {
          this.inputData = false
        }
      },
      next: function () {
        var self = this
        if ($('.account').val() !== '' || $('.password').val() !== '') {
          this.error = false
          if (window.screen.width < 768) {
            this.error2 = false
          }
          this.inputData = false
          this.inputStatus = false
          if (this.page === 0) {
            $.ajax({
              async: false,
              url: 'sign_in_out.php',
              type: 'POST',
              data: {account: $('.account').val(), step1: 'step1'},
              success: function (data) {
                self.signMode = data
                self.button = data
                self.account = $('.account').val()
                console.log(self.account)
              }
            })
          }
          this.page++
          this.prompt = 'Enter password to join us'
          if (this.page === 2) {
            this.password = $('.password').val()
            this.page--
            this.check = true
            setTimeout(function () {
              $('#check').focus()
            }, 300)
            // checkCookie(1, 'DEAN')
            // self.$root.eventHub.$emit('sign')
            // setTimeout(function () {
            //   self.$router.push({path: '/home'})
            // }, 1800)
          } else if (this.page === 1) {
            setTimeout(function () {
              $('.password').focus()
            }, 100)
            if (window.screen.width < 768) {
              $('.password').attr('placeholder', 'Enter your password')
            }
          }
        } else {
          this.error = true
          if (window.screen.width < 768) {
            this.error2 = true
          }
        }
      },
      previous: function () {
        this.error = false
        $('.password').val('')
        this.inputData = false
        this.inputStatus = false
        this.page--
        setTimeout(function () {
          $('.account').focus()
        }, 100)
      },
      back: function () {
        this.$router.push({path: '/home'})
        this.inputData = false
        this.inputStatus = false
        this.inputStatus3 = false
        this.error = false
        this.error2 = false
        this.error3 = false
        $('.account').val('')
        $('.password').val('')
      },
      submit: function () {
        var returnData = ''
        var temp = $('#check').val()
        var self = this
        $.ajax({
          async: false, // 异步处理，使ajax先执行
          url: 'form.php',
          type: 'POST',
          data: {code: temp},
          success: function (data) {
            console.log(temp)
            console.log(self.account + ' ' + self.password)
            if (data === '正确') {
              $.ajax({
                url: 'sign_in_out.php',
                type: 'POST',
                data: {account: self.account, psd: self.password, step2: self.signMode},
                success: function (data) {
                  console.log(data)
                  self.error3 = false
                  self.inputStatus3 = true
                  self.check = false
                  self.page = 2
                  checkCookie(1, 'DEAN')
                  self.$root.eventHub.$emit('sign')
                  setTimeout(function () {
                    self.$router.push({path: '/home'})
                  }, 1800)
                  $('#signBox input').val('')
                }
              })
            } else {
              self.error3 = true
            }
          }
        })
        return returnData
      },
      change: function () {
        $('#img').attr('src', 'checkcode.php?r=' + Math.random())
      },
      cancelCheck: function () {
        this.check = false
        this.error3 = false
        this.inputStatus3 = false
      }
    },
    computed: {
      resultRender: function () {
        if (this.result === 1) {
          return 'to sign in'
        } else {
          return 'to sign up'
        }
      },
      mobile: function () {
        if (window.screen.width < 768 && this.inputStatus) {
          return false
        } else {
          return true
        }
      }
    },
    mounted () {
      if (window.screen.width < 768) {
        $('.account').attr('placeholder', 'Email or phone')
        this.$root.eventHub.$emit('nav', 3)
      } else {
        setTimeout(function () {
          $('.account').focus()
        }, 100)
      }
      var self = this
      self.$root.eventHub.$emit('loading')
      setTimeout(function () {
        self.loaded = true
        self.$root.eventHub.$emit('load')
      }, 1000)
    }
  }
</script>

