      }
      100%{
        -webkit-transform: translateX(0);
      }
    }
    @-moz-keyframes enter {
      0%{
        -moz-transform: translateX(100%);
      }
      100%{
        -moz-transform: translateX(0);
      }
    }
    @-o-keyframes enter {
      0%{
        -o-transform: translateX(100%);
      }
      100%{
        -o-transform: translateX(0);
      }
    }
    @keyframes enter {
      0%{
        transform: translateX(100%);
      }
      100%{
        transform: translateX(0);
      }
    }
    @-webkit-keyframes leave {
      0%{
        -webkit-transform: translateX(0);
      }
      100%{
        -webkit-transform: translateX(-100%);
      }
    }
    @-moz-keyframes leave {
      0%{
        -moz-transform: translateX(0);
      }
      100%{
        -moz-transform: translateX(-100%);
      }
    }
    @-o-keyframes leave {
      0%{
        -o-transform: translateX(0);
      }
      100%{
        -o-transform: translateX(-100%);
      }
    }
    @keyframes leave {
      0%{
        transform: translateX(0);
      }
      100%{
        transform: translateX(-100%);
      }
    }
    .next{
      width: 88px;
      height: 36px;
      border-radius: 3px;
      background: #1296db;
      box-shadow: 0 1px 2px grey;
      margin: 15px 0;
      float: right;
      text-align: center;
      font-size: 14px;
      font-weight: bold;
      line-height: 36px;
      color: white;
      cursor: pointer;
      transition: all .2s;
    }
    #in-or-up .next{
      margin: 50px 0;
    }
    #in-or-up #back{
      float: left;
      margin: 50px 4px;
    }
    .next:active{
      box-shadow: 0 3px 12px grey;
      background: #1E90FF;
    }
    #welcome{
      font-size: 24px;
      padding: 13px 3px 0;
    }
    #up-prompt{
      font-size: 14px;
      height: 24px;
      padding: 8px 3px 0;
    }
    #success-img{
      width: 90px;
      margin: 100px calc((100% - 90px) / 2) 16px;
      -webkit-animation: up .4s;
      -moz-animation: up .4s;
      -o-animation: up .4s;
      animation: up .4s;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    .success-text{
      width: 100%;
      text-align: center;
      font-size: 24px;
      color: #333333;
    }
    #footer p{
      font-size: 14px;
      color: #696969;
      cursor: pointer;
    }
    #footer p:first-of-type{
      float: left;
    }
    #footer p:not(:first-of-type){
      float: right;
      margin-left: 24px;
    }
    .checkBox{
      width: 210px;
      height: 228px;
      padding: 15px;
      background: white;
      position: fixed;
      top: calc((100% - 278px) / 2);
      left: calc((100% - 240px) / 2);
      z-index: 50;
    }
    .checkBox p{
      font-size: 18px;
    }
    .checkBox img{
      width: 210px;
      height: 60px;
      margin: 20px 0 0;
    }
    .checkBox input:first-of-type{
      width: 210px;
      height: 40px;
      border: none;
      outline: none;
      border-bottom: 1px solid #d9d9d9;
      margin-top: 12px;
      font-size: 14px;
      line-height: 40px;
    }
    .checkBox input:last-of-type{
      width: 210px;
      height: 40px;
      border: none;
      outline: none;
      margin-top: 30px;
      border-radius: 3px;
      background: #1296db;
      box-shadow: 0 1px 2px grey;
      color: white;
      font-size: 14px;
      font-weight: bold;
      cursor: pointer;
    }
  }

  @media (max-width: 599px) and (min-device-width: 768px) {
    #sign{
      overflow-y: scroll;
    }
    #sign svg{
      display: none;
    }
    #signBox{
      width: calc(100% - 80px);
      height: calc(100% - 72px);
      position: absolute;
      top: 0;
      left: 0;
    }
    #footer{
      width: calc(100% - 80px);
      height: 30px;
      padding: 10px 40px 10px;
      overflow: hidden;
      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 3;
      background: white;
    }
    #footer p{
      margin-top: 8px;
    }
    #footer p:nth-of-type(2){
      margin-right: 20px;
    }
  }

  @media (min-width: 600px) and (min-device-width: 768px) {
    #signBox{
      width: 370px;
      height: 428px;
      position: absolute;
      top: calc((100% - 500px) / 2);
      left: calc((100% - 450px) / 2);
      box-shadow: 0 1px 2px #707070;
    }
    #footer{
      width: 450px;
      height: 50px;
      position: absolute;
      top: calc((100% - 500px) / 2 + 500px);
      left: calc((100% - 450px) / 2);
    }
    #footer p{
      margin-top: 24px;
    }
  }

  @media (min-width: 600px) and (max-height: 600px) and (min-device-width: 768px) {
    html{
      height: 100%;
    }
    #footer{
      padding: 0 0 12px;
    }
    #sign{
      overflow-y: scroll;
    }
    #sign svg{
      position: fixed;
    }
    #signBox{
      top: 34px;
    }
    #footer{
      top: 534px;
      z-index: 3;
    }
  }
</style>



