<script>
  import $ from 'jquery'
  import {checkCookie, clearCookie} from './js/cache.js'
  export default {
    name: 'app',
    data () {
      return {
        showBar: false,
        nav: '',
        logo: '',
        Title: 'MyTube',
        index: 0,
        index2: 0,
        signStatus: false,
        Sign: 'Sign in',
        username: 'DEAN YE',
        email: 'deanye2017@gmail.com',
        shortCut: false,
        pc: true,
        prompt: false,
        promptText: 'Watch history isn\'t viewable when signed out.',
        promptTitle: 'Fail',
        loaded: false,
        searchStatus: false,
        searchData: []
      }
    },
    methods: {
      Bar: function () {
        if (window.screen.width >= 768) {
          if (window.innerWidth < 1294) {
            this.showBar = !this.showBar
          } else {
            if (this.$route.path === '/home') {
              this.$root.eventHub.$emit('bar')
            } else if (this.$route.path === '/user') {
              this.$root.eventHub.$emit('bar2')
            } else if (this.$route.path === '/history') {
              this.$root.eventHub.$emit('bar3')
            } else {
              this.showBar = !this.showBar
            }
          }
        } else {
          this.showBar = !this.showBar
        }
      },
      Nav: function (index) {
        this.loaded = false
        switch (index) {
          case 0:
            this.Title = 'MyTube'
            this.$router.push({path: '/'})
            break
          case 1:
            this.Title = 'Trending'
            this.$router.push({path: '/hot'})
            break
          case 2:
            this.Title = 'Interest'
            this.$router.push({path: '/category'})
            break
          case 3:
            this.$router.push({path: '/sign'})
            break
          case 4:
            this.$router.push({path: '/user'})
            break
        }
        this.index = index
      },
      navIcon: function (i) {
        if (i === this.index) {
          return true
        } else {
          return false
        }
      },
      sign: function () {
        this.Nav(3)
        if (this.showBar) {
          this.showBar = false
        }
        if (this.prompt) {
          this.prompt = false
        }
      },
      sign2: function () {
        if (!this.signStatus) {
          this.Nav(3)
        } else {
          this.Title = 'My Channel'
          this.index = 4
          this.$router.push({path: '/user'})
        }
        if (this.showBar) {
          this.showBar = false
        }
      },
      home: function (i) {
        if (window.screen.width >= 768 || i === 0) {
          this.Nav(0)
        }
        if (i === 2 || window.screen.width < 768) {
          this.showBar = false
        }
        this.showHistory = false
        this.index2 = 0
      },
      user: function () {
        this.shortCut = !this.shortCut
      },
      clear: function () {
        clearCookie()
        this.shortCut = !this.shortCut
        this.signStatus = false
        this.Sign = 'Sign in'
      },
      center: function () {
        this.$root.eventHub.$emit('hideHistory')
        this.$router.push({path: '/user'})
        this.index2 = -1
        this.shortCut = !this.shortCut
      },
      hide: function () {
        if (event.target.id !== 'sign-user' && this.shortCut) {
          this.shortCut = false
        }
      },
      history: function () {
        this.showBar = false
        if (checkCookie(2, '')) {
          this.index2 = 3
          this.$router.push({path: '/history'})
          this.$root.eventHub.$emit('full', false)
          if (this.$route.path === '/video') {
            this.$root.eventHub.$emit('pause')
          }
          if (window.screen.width < 768) {
            this.Title = 'History'
          }
        } else {
          var self = this
          setTimeout(function () {
            self.prompt = true
            setTimeout(function () {
              if (self.prompt) {
                self.prompt = false
                setTimeout(function () {
                  self.$router.push({path: '/sign'})
                }, 200)
              }
            }, 1000)
          }, 500)
        }
      },
      cancelPrompt: function () {
        this.prompt = false
      },
      fixedNav: function (i) {
        if (i === this.index2) {
          return true
        } else {
          return false
        }
      },
      creator: function () {
        this.$router.push({path: '/creator'})
        this.shortCut = !this.shortCut
      },
      search: function () {
        this.searchStatus = true
      },
      searching: function () {
        var self = this
        var temp = $('#search').val()
        if (temp !== '') {
          $.ajax({
            url: 'search_content.php',
            type: 'POST',
            data: {search_content: temp},
            success: function (data) {
              self.searchData = data.split('@')
              console.log(self.searchData)
            }
          })
        } else {
          self.searchData = ['NULL']
        }
      },
      cancelSearch: function () {
        var self = this
        $('#search').val('')
        this.searchData = []
        setTimeout(function () {
          self.searchStatus = false
        }, 500)
      },
      renderText: function (item) {
        var temp
        var reg = new RegExp('-', 'g')
        if (item === 'NULL' || item === 'NULL ') {
          temp = 'No such element.'
        } else {
          temp = item.substring(0, item.length - 4).replace(reg, ' ')
        }
        return temp
      },
      watchVideo: function (i) {
        this.$root.src = this.searchData[i]
        this.cancelSearch()
        this.$root.eventHub.$emit('loading')
        this.$router.push({path: '/video'})
      }
    },
    computed: {
      userHead: function () {
        if (this.$root.head === '') {
          return 'background: url(\'static/assets/user-circle3.jpg\')'
        } else {
          return 'background: url(\'' + this.$root.head + '\')'
        }
      }
    },
    mounted () {
      var self = this
      self.signStatus = checkCookie(2, '')
      if (self.signStatus) {
        self.Sign = 'My Channel'
      }
      if (window.screen.width >= 768) {
        this.nav = require('../static/assets/nav.png')
        this.logo = require('../static/assets/logo.png')
      } else {
        this.nav = require('../static/assets/nav-white.png')
        this.logo = require('../static/assets/logo-white.png')
      }
      this.$root.eventHub.$on('nav', function (i) {
        if (window.screen.width < 768) {
          self.index = i
          switch (i) {
            case 0:
              self.Title = 'MyTube'
              break
            case 1:
              self.Title = 'Trending'
              break
            case 2:
              self.Title = 'Interest'
              break
            case 4:
              self.Title = 'My Channel'
              break
            case 5:
              self.Title = 'History'
              break
          }
        }
      })
      this.$root.eventHub.$on('sign', function () {
        self.Sign = 'My Channel'
        self.signStatus = checkCookie(2, '')
      })
      this.$root.eventHub.$on('user', function () {
        if (self.shortCut) {
          self.shortCut = !self.shortCut
        }
      })
      this.$root.eventHub.$on('resetIndex2', function () {
        self.index2 = -1
      })
      this.$root.eventHub.$on('clear', function () {
        self.clear()
      })
      this.$root.eventHub.$on('prompt', function (data) {
        self.prompt = true
        if (data === 'center') {
          self.promptText = 'User channel isn\'t viewable when signed out.'
        } else {
          self.promptText = 'Watch history isn\'t viewable when signed out.'
        }
      })
      this.$root.eventHub.$on('loading', function () {
        self.loaded = false
      })
      this.$root.eventHub.$on('load', function () {
        self.loaded = true
      })
      if (window.screen.width < 768) {
        if (this.$route.path === '/home') {
          self.index = 0
          self.Title = 'MyTube'
        } else if (this.$route.path === '/hot') {
          self.index = 1
          self.Title = 'Trending'
        } else if (this.$route.path === '/category') {
          self.index = 2
          self.Title = 'Interest'
        } else if (this.$route.path === '/user') {
          self.index = 4
          self.Title = 'My Channel'
        } else if (this.$route.path === '/history') {
          self.index = 5
          self.Title = 'History'
        }
      }
      this.pc = this.$root.adapt
    }
  }
</script>

