    cursor: pointer;
  }
  #app{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
     -webkit-font-smoothing: antialiased;
     -moz-osx-font-smoothing: grayscale;
  }

  #navBar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
  }
  #fixed-nav{
    position: fixed;
    left: 0;
  }
  .grey, .grey2{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.6;
  }
  .grey2{
    opacity: 0;
  }
  .modal-enter-active{
    -webkit-animation: modal .2s;
    -moz-animation: modal .2s;
    -o-animation: modal .2s;
    animation: modal .2s;
  }
  .modal-leave-active{
    -webkit-animation: modal .2s reverse;
    -moz-animation: modal .2s reverse;
    -o-animation: modal .2s reverse;
    animation: modal .2s reverse;
  }
  @-webkit-keyframes modal {
    0%{
      -webkit-transform: translate(-50%, -30px);
    }
    100%{
      -webkit-transform: translate(-50%, 0);
    }
  }
  @-moz-keyframes modal {
    0%{
      -moz-transform: translate(-50%, -30px);
    }
    100%{
      -moz-transform: translate(-50%, 0);
    }
  }
  @-o-keyframes modal {
    0%{
      -o-transform: translate(-50%, -30px);
    }
    100%{
      -o-transform: translate(-50%, 0);
    }
  }
  @keyframes modal {
    0%{
      transform: translate(-50%, -30px);
    }
    100%{
      transform: translate(-50%, 0);
    }
  }
  .promptBox{
    height: auto;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    background: white;
  }

  @media (max-device-width: 767px) {
    #navBar{
      height: 130px;
      background: #1296db;
      z-index: 10;
    }
    .nav{
      width: 125px;
      height: 110px;
      margin: 10px 20px 0 0;
      float: right;
    }
    .nav img{
      width: 90px;
      margin: 10px 20px 10px 15px;
    }
    .logo{
      width: 115px;
      height: 110px;
      float: left;
      margin: 10px 20px 0 30px;
    }
    .logo img{
      width: 90px;
      margin: 10px 15px 10px 10px;
    }
    .webName{
      font-size: 54px;
      font-weight: bold;
      line-height: 130px;
      color: white;
      float: left;
    }
    #search, #search-button, #sign-button, #nav-setting{
      display: none;
    }
    #nav{
      width: 100%;
      height: 112px;
      background: #1296db;
      margin: 128px 0 0;
      z-index: 6;
    }
    #nav-table{
      width: 94%;
      height: 100%;
      margin: 0 3%;
      overflow: hidden;
      table-layout: fixed;
    }
    #nav-table th{
      display: table-cell;
      vertical-align: inherit;
    }
    .navIcon.home{
      width: 70px;
      height: 70px;
      margin: 0 calc((100% - 70px) / 2) 8px;
      background: url("../static/assets/home-grey.png");
      background-size: cover;
    }
    .navIcon.home.active{
      background: url("../static/assets/home.png");
      background-size: cover;
    }
    .navIcon.hot{
      width: 75px;
      height: 75px;
      margin: 0 calc((100% - 75px) / 2) 8px;
      background: url("../static/assets/hot-grey.png");
      background-size: cover;
    }
    .navIcon.hot.active{
      background: url("../static/assets/hot.png");
      background-size: cover;
    }
    .navIcon.interest{
      width: 80px;
      height: 80px;
      margin: 0 calc((100% - 80px) / 2) 7px;
      background: url("../static/assets/category-grey.png");
      background-size: cover;
    }
    .navIcon.interest.active{
      background: url("../static/assets/category.png");
      background-size: cover;
    }
    .navIcon.center{
      width: 64px;
      height: 64px;
      margin: 0 calc((100% - 64px) / 2) 7px;
      background: url("../static/assets/center-grey.png");
      background-size: cover;
    }
    .navIcon.center.active{
      background: url("../static/assets/center.png");
      background-size: cover;
    }
    #fixed-nav{
      width: calc(100% - 20px);
      height: auto;
      padding: 0 10px;
      top: 128px;
      background: #1296db;
      box-shadow: 0 3px 5px #808080;
      z-index: 8;
    }
    .bar-enter-active{
      -webkit-animation: bar .2s;
      -moz-animation: bar .2s;
      -o-animation: bar .2s;
      animation: bar .2s;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    .bar-leave-active{
      -webkit-animation: bar .2s reverse;
      -moz-animation: bar .2s reverse;
      -o-animation: bar .2s reverse;
      animation: bar .2s reverse;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    @-webkit-keyframes bar {
      0%{
        -webkit-transform: translateY(-100%);
      }
      100%{
        -webkit-transform: translateY(0);
      }
    }
    @-moz-keyframes bar {
      0%{
        -moz-transform: translateY(-100%);
      }
      100%{
        -moz-transform: translateY(0);
      }
    }
    @-o-keyframes bar {
      0%{
        -o-transform: translateY(-100%);
      }
      100%{
        -o-transform: translateY(0);
      }
    }
    @keyframes bar {
      0%{
        transform: translateY(-100%);
      }
      100%{
        transform: translateY(0);
      }
    }
    #fixed-nav-header{
      display: none;
    }
    #fixed-nav-bar{
      width: 100%;
      height: auto;
    }
    #fixed-nav-content{
      width: 100%;
      height: auto;
      padding: 12px 0 0;
    }
    #fixed-nav-content>div{
      width: calc(100% - 80px);
      height: 130px;
      margin: 0 40px;
      color: white;
      text-align: center;
    }
    #fixed-nav-content>div:not(:last-of-type){
      border-top: 3px solid #f0f2f5;
    }
    #fixed-nav-content>div svg{
      display: none!important;
    }
    .category-item p{
      font-size: 48px;
      line-height: 130px;
    }
    .grey{
      z-index: 6;
    }
    .grey3{
      z-index: 10;
    }
    .grey2{
      display: none;
    }
    .promptBox{
      width: calc(100% - 80px);
      top: 40px;
      z-index: 12;
      border-radius: 12px;
    }
    .prompt-content p{
      font-size: 42px;
      text-align: center;
      padding: 42px 0;
    }
    .prompt-header, .prompt-content>div{
      display: none;
    }
  }

  @media (min-device-width: 768px) {
    .load-progress{
      position: fixed;
      top: 0;
      z-index: 10;
      width: 100%;
      height: 4px;
    }
    .load-progress>div{
      width: 100%;
      height: 2px;
      background: #1296db;
      -webkit-animation: load 1.8s infinite;
      -moz-animation: load 1.8s infinite;
      -o-animation: load 1.8s infinite;
      animation: load 1.8s infinite;
    }
    @-webkit-keyframes load {
      0%{
        -webkit-transform: translateX(-100%);
      }
      100%{
        -webkit-transform: translateX(100%);
      }
    }
    @-moz-keyframes load {
      0%{
        -moz-transform: translateX(-100%);
      }
      100%{
        -moz-transform: translateX(100%);
      }
    }
    @-o-keyframes load {
      0%{
        -o-transform: translateX(-100%);
      }
      100%{
        -o-transform: translateX(100%);
      }
    }
    @keyframes load {
      0%{
        transform: translateX(-100%);
      }
      100%{
        transform: translateX(100%);
      }
    }
    .load-progress.loaded>div{
      display: none;
    }
    #navBar{
      height: 56px;
      z-index: 4;
      background: white;
      box-shadow: 0 2px 5px #EBEBEB;
    }
    .nav{
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin: 8px 0 8px 10px;
      float: left;
    }
    .nav img{
      width: 24px;
      margin: 8px;
    }
    .logo{
      width: 52px;
      height: 39px;
      float: left;
      margin: 8px 0;
    }
    .logo img{
      width: 60%;
      margin: 4px 20%;
    }
    .webName{
      font-size: 18px;
      font-weight: bold;
      margin: 19px 6px 19px 0;
      float: left;
    }
    #search{
      display: none;
      width: calc(100% - 480px);
      height: 26px;
      font-size: 16px;
      margin: 13px 0 11px 22px;
      padding: 0px 10px 3px;
      border: 1px solid #C0C0C0;
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
      float: left;
      position: relative;
      z-index: 0;
    }
    #search:focus{
      border: 1px solid #1296db;
      z-index: 2;
    }
    .comment::-moz-selection{
      color: white;
      background-color: #1296db;
    }
    #search::selection{
      color: white;
      background-color: #1296db;
    }
    #search-button{
      display: none;
      width: 64px;
      height: 29px;
      border: 1px solid #C0C0C0;
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
      float: left;
      margin: 13px 0 11px 0;
      background: #F8F8F8;
      position: relative;
      right: 2px;
      z-index: 1;
    }
    #search-button:hover{
      background: #F0F0F0;
    }
    #search-button div{
      width: 18px;
      height: 18px;
      margin: 6px 24px 5px 22px;
      background: url("../static/assets/search-grey2.png");
      background-size: cover;
    }
    #search-button:hover div{
      background: url("../static/assets/search.png");
      background-size: cover;
    }
    #sign-button{
      float: right;
      width: 80px;
      height: 40px;
      border-radius: 3px;
      text-align: center;
      margin: 8px 12px 8px 5px;
    }
    #sign-button p{
      font-size: 15px;
      font-weight: bold;
      padding: 13px 0 12px 0;
      color: #1296db;
    }
    #sign-user{
      width: 32px;
      height: 32px;
      border-radius: 25px;
      margin: 12px 16px 12px 5px;
      background-position: 50%!important;
      background-size: cover!important;
      float: right;
    }
    #sign-user:active{
      border: 1px solid #1296db;
      margin: 11px 15px 12px 4px;
    }
    #nav-setting{
      float: right;
      width: 40px;
      height: 40px;
      margin: 8px 0 8px 0;
      border-radius: 20px;
    }
    #nav-setting div{
      width: 18px;
      height: 18px;
      margin: 11px;
      background: url("../static/assets/setting.png");
      background-size: cover;
    }
    #nav{
      display: none;
    }
    .nav:active, #nav-setting:active, #sign-button:active{
      background: #E4E4E4;
    }
    #fixed-nav{
      width: 240px;
      height: 100%;
      top: 0;
      z-index: 42;
      background: #F5F5F5;
    }
    #fixed-nav-bar{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 58px);
      overflow-y: scroll;
    }
    #fixed-nav-content{
      width: 100%;
      height: 1000px;
    }
    #fixed-nav-bar::-webkit-scrollbar{
      width: 8px;
      display: none;
    }
    #fixed-nav-bar:hover::-webkit-scrollbar{
      display: inline;
    }
    #fixed-nav-bar::-webkit-scrollbar-thumb{
      border-radius: 5px;
      background: #C8C8C8;
    }
    #fixed-nav-bar::-webkit-scrollbar-track{
      border-radius: 4px;
      background: transparent;
    }
    #fixed-nav-header{
      width: 100%;
      height: 56px;
      border-bottom: 2px solid #EBEBEB;
    }
    .bar-enter-active{
      -webkit-animation: bar .2s;
      -moz-animation: bar .2s;
      -o-animation: bar .2s;
      animation: bar .2s;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    .bar-leave-active{
      -webkit-animation: bar .2s reverse;
      -moz-animation: bar .2s reverse;
      -o-animation: bar .2s reverse;
      animation: bar .2s reverse;
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    @-webkit-keyframes bar {
      0%{
        -webkit-transform: translateX(-240px);
      }
      100%{
        -webkit-transform: translateX(0px);
      }
    }
    @-moz-keyframes bar {
      0%{
        -moz-transform: translateX(-240px);
      }
      100%{
        -moz-transform: translateX(0px);
      }
    }
    @-o-keyframes bar {
      0%{
        -o-transform: translateX(-240px);
      }
      100%{
        -o-transform: translateX(0px);
      }
    }
    @keyframes bar {
      0%{
        transform: translateX(-240px);
      }
      100%{
        transform: translateX(0px);
      }
    }
    .category-item{
      width: calc(100% - 48px);
      height: 40px;
      padding: 1px 24px 0;
      overflow: hidden;
      cursor: pointer;
    }
    .category-item:hover{
      background: #EBEBEB;
    }
    .category-item:nth-of-type(2){
      margin-top: 10px;
    }
    .category-item:nth-of-type(4), .category-item:nth-of-type(7), .category-item:nth-of-type(8){
      margin-bottom: 10px;
    }
    .category-item svg{
      width: 24px!important;
      height: 24px!important;
      fill: #888888;
      padding: 8px 26px 8px 0;
      float: left;
    }
    .category-item.active svg{
      fill: #1296db;
    }
    .category-item p{
      font-size: 15px;
      line-height: 40px;
      color: #444444;
      float: left;
    }
    .category-item.active p{
      font-weight: bold;
    }
    .part{
      width: calc(100% - 48px);
      height: 24px;
      display: block;
      padding: 12px 24px 4px;
      border-top: 1px solid #EEEEEE;
      font-size: 14px;
      line-height: 24px;
      color: #8C8C8C;
    }
    .sub{
      width: 24px;
      height: 24px;
      border-radius: 12px;
      margin: 8px 26px 8px 0;
      background-position: 50%!important;
      background-size: cover!important;
      float: left;
    }
    #fixed-nav-content>p:last-of-type{
      font-size: 14px;
      color: #8C8C8C;
      width: calc(100% - 48px);
      padding: 0 24px;
    }
    .grey{
      z-index: 40;
    }
    .grey2{
      z-index: 4;
    }
    .grey4{
      z-index: 2;
    }
    #user-shortcut{
      width: 300px;
      height: auto;
      position: fixed;
      top: 48px;
      right: 16px;
      background: white;
      z-index: 5;
      box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.4);
    }
    .header{
      width: 100%;
      height: 72px;
      background: #EEEEEE;
      position: relative;
      margin-bottom: 8px;
      cursor: pointer;
    }
    .header .pic{
      width: 40px;
      height: 40px;
      border-radius: 22px;
      position: absolute;
      top: 16px;
      left: 16px;
      background-position: 50%!important;
      background-size: cover!important;
    }
    .header p:first-of-type, .header p:nth-of-type(2){
      position: absolute;
      left: 72px;
    }
    .header p:first-of-type{
      font-size: 16px;
      top: 20px;
      font-weight: bold;
      color: #111111;
    }
    .header p:nth-of-type(2){
      font-size: 14px;
      top: 40px;
      color: #111111;
    }
    .button{
      width: 270px;
      height: 40px;
      padding: 0 15px;
      overflow: hidden;
      cursor: pointer;
    }
    .button:hover, .button:active{
      background: #F5F5F5;
    }
    .button:last-of-type{
      margin-bottom: 8px;
    }
    .button svg{
      width: 24px!important;
      height: 24px!important;
      padding: 8px 0;
      fill: #888888;
      float: left;
    }
    .button p{
      font-size: 15px;
      color: #525252;
      float: left;
      line-height: 40px;
      margin-left: 34px;
    }
    .promptBox{
      width: auto;
      background: white;
      box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.4);
      border-radius: 3px;
      top: 30px;
      z-index: 50;
    }
    .prompt-header p{
      font-size: 16px;
      padding: 14px 30px 4px;
    }
    .promptBox .close{
      width: 20px;
      height: 20px;
      float: right;
      position: absolute;
      top: 12px;
      right: 30px;
      background: url("../static/assets/delete-grey.png") 50% no-repeat;
      background-size: 75%;
      cursor: pointer;
    }
    .prompt-header, .prompt-content{
      width: auto;
      height: auto;
    }
    .prompt-content p{
      font-size: 18px;
      text-align: center;
      line-height: 24px;
      padding: 6px 30px 5px;
    }
    .prompt-content>div{
      float: right;
      background: #1296db;
      color: white;
      font-size: 15px;
      width: auto;
      height: auto;
      border-radius: 3px;
      padding: 8px 12px;
      margin: 2px 30px 12px 0;
      box-shadow: 0 1px 2px grey;
      cursor: pointer;
    }
    .search-wrapper{
      position: fixed;
      top: 42px;
      width: calc(100% - 460px);
      height: auto;
      padding: 16px 0;
      left: 200px;
      z-index: 3;
    }
    .search-wrapper::-webkit-scrollbar{
      display: none;
    }
    .search-item{
      width: calc(100% - 48px);
      height: auto;
      padding: 16px 24px;
      border-radius: 3px;
      margin: 8px 0;
      background: white;
      font-size: 16px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      cursor: pointer;
    }
    .search-enter-active{
      -webkit-animation: down .2s;
      -moz-animation: down .2s;
      -o-animation: down .2s;
      animation: down .2s;
    }
    .search-leave-active{
      -webkit-animation: down .2s reverse;
      -moz-animation: down .2s reverse;
      -o-animation: down .2s reverse;
      animation: down .2s reverse;
    }
    @-webkit-keyframes down {
      0%{
        -webkit-transform: translateY(-40px);
      }
      100%{
        -webkit-transform: translateY(0);
      }
    }
    @-moz-keyframes down {
      0%{
        -moz-transform: translateY(-40px);
      }
      100%{
        -moz-transform: translateY(0);
      }
    }
    @-o-keyframes down {
      0%{
        -o-transform: translateY(-40px);
      }
      100%{
        -o-transform: translateY(0);
      }
    }
    @keyframes down {
      0%{
        transform: translateY(-40px);
      }
      100%{
        transform: translateY(0);
      }
    }
  }

  @media (min-device-width: 768px) and (min-width: 616px) {
    #search, #search-button{
      display: inline;
    }
  }

  @media (min-device-width: 768px) and (min-width: 768px) {
    #search{
      width: calc(100% - 600px);
      margin: 13px 0 11px 52px;
    }
    .search-wrapper{
      left: 240px;
      width: calc(100% - 580px);
    }
    .nav{
      margin: 8px 5px 8px 15px;
    }
  }

  @media (min-device-width: 768px) and (min-width: 992px) {
    #search{
      width: calc(100% - 640px);
    }
    .search-wrapper{
      width: calc(100% - 620px);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1200px) {
    #search{
      width: 570px;
    }
    .search-wrapper{
      width: 590px;
    }
  }
</style>



