<template>
  <div id="home">
    <div v-show="showBar" id="category">
      <div id="category-content">
        <div class="category-item" :class="{'active': fixedNav(0)}" @click="home">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8" class="style-scope yt-icon"></path>
          </g></svg>
          <p>Home</p>
        </div>
        <div class="category-item" :class="{'active': fixedNav(1)}">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M17.53 11.2c-.23-.3-.5-.56-.76-.82-.65-.6-1.4-1.03-2.03-1.66-1.46-1.46-1.78-3.87-.85-5.72-.9.23-1.75.75-2.45 1.32C8.9 6.4 7.9 10.07 9.1 13.22c.04.1.08.2.08.33 0 .22-.15.42-.35.5-.22.1-.46.04-.64-.12-.06-.05-.1-.1-.15-.17-1.1-1.43-1.28-3.48-.53-5.12C5.87 10 5 12.3 5.12 14.47c.04.5.1 1 .27 1.5.14.6.4 1.2.72 1.73 1.04 1.73 2.87 2.97 4.84 3.22 2.1.27 4.35-.12 5.96-1.6 1.8-1.66 2.45-4.3 1.5-6.6l-.13-.26c-.2-.45-.47-.87-.78-1.25zm-3.1 6.3c-.28.24-.73.5-1.08.6-1.1.38-2.2-.16-2.88-.82 1.2-.28 1.9-1.16 2.1-2.05.17-.8-.14-1.46-.27-2.23-.12-.74-.1-1.37.2-2.06.15.38.35.76.58 1.06.76 1 1.95 1.44 2.2 2.8.04.14.06.28.06.43.03.82-.32 1.72-.92 2.26z" class="style-scope yt-icon"></path>
          </g></svg>
          <p>Trending</p>
        </div>
        <div class="category-item" :class="{'active': fixedNav(2)}">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M18.7 8.7H5.3V7h13.4v1.7zm-1.7-5H7v1.6h10V3.7zm3.3 8.3v6.7c0 1-.7 1.6-1.6 1.6H5.3c-1 0-1.6-.7-1.6-1.6V12c0-1 .7-1.7 1.6-1.7h13.4c1 0 1.6.8 1.6 1.7zm-5 3.3l-5-2.7V18l5-2.7z" class="style-scope yt-icon"></path>
          </g></svg>
          <p>Subscriptions</p>
        </div>
        <p class="part">LIBRARY</p>
        <div class="category-item" :class="{'active': fixedNav(3)}" @click="history">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M11.9 3.75c-4.55 0-8.23 3.7-8.23 8.25H.92l3.57 3.57.04.13 3.7-3.7H5.5c0-3.54 2.87-6.42 6.42-6.42 3.54 0 6.4 2.88 6.4 6.42s-2.86 6.42-6.4 6.42c-1.78 0-3.38-.73-4.54-1.9l-1.3 1.3c1.5 1.5 3.55 2.43 5.83 2.43 4.58 0 8.28-3.7 8.28-8.25 0-4.56-3.7-8.25-8.26-8.25zM11 8.33v4.6l3.92 2.3.66-1.1-3.2-1.9v-3.9H11z" class="style-scope yt-icon"></path>
          </g></svg>
          <p>History</p>
        </div>
        <div class="category-item" :class="{'active': fixedNav(4)}">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M12 3.67c-4.58 0-8.33 3.75-8.33 8.33s3.75 8.33 8.33 8.33 8.33-3.75 8.33-8.33S16.58 3.67 12 3.67zm3.5 11.83l-4.33-2.67v-5h1.25v4.34l3.75 2.25-.67 1.08z" class="style-scope yt-icon"></path>
          </g></svg>
          <p>Watch later</p>
        </div>
        <div class="category-item" :class="{'active': fixedNav(5)}">
          <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" focusable="false" class="style-scope yt-icon" style="pointer-events: none; display: block; width: 100%; height: 100%;"><g class="style-scope yt-icon">
            <path d="M3.75 18.75h3v-9h-3v9zm16.5-8.25c0-.83-.68-1.5-1.5-1.5h-4.73l.7-3.43.03-.24c0-.3-.13-.6-.33-.8l-.8-.78L8.7 8.7c-.3.26-.45.64-.45 1.05v7.5c0 .82.67 1.5 1.5 1.5h6.75c.62 0 1.15-.38 1.38-.9l2.27-5.3c.06-.18.1-.36.1-.55v-1.5z" class="style-scope yt-icon"></path>
          </g></svg>
          <p>Liked videos</p>
        </div>
        <p class="part">SUBSCRIPTIONS</p>
        <div class="category-item">
          <div class="sub" style="background: url('../../static/image/photo.jpg');"></div>
          <p>Jason Chen</p>
        </div>
        <p class="part"></p>
        <p>&copy;2018 MyTube, LLC</p>
      </div>
    </div>
    <div v-if="this.$root.adapt && loaded" class="home-content" :class="{'showContent': showBar}">
      <div v-for="(item, index) in category" class="category" :class="{'showCategory': showBar}">
        <p class="catName">{{item.name}}</p>
        <div class="home-content-wrapper">
          <div v-for="i in 6" @click="video(index, i)">
            <div class="picture" :style="picture(index, i)" @mouseenter="animate(index, i)" @mouseout="cancelAnimate(index, i)"></div>
            <p>{{renderText(index, i, 0)}}</p>
            <p>{{renderAuthor(index, i, 0)}}</p>
            <p>{{renderViews(index, i)}}</p>
            <p>{{renderTime(index, i)}}</p>
          </div>
        </div>
      </div>
    </div>
    <div v-if="!this.$root.adapt" class="mobile-content">
      <div v-for="i in 10" @click="video2(i)">
        <div class="video-pic" :style="picture(1, i)"></div>
        <div class="video-blogger" style="background: url('static/assets/2.png')"></div>
        <div class="video-intro">
          <p class="video-intro-title">2017 in 7 minutes</p>
          <p class="video-intro-blogger">Vine Mansion</p>
          <p class="video-intro-views">15M views</p>
          <p class="video-intro-time">1 year ago</p>
        </div>
        <div class="watch-later" @click="watchLater"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import {checkCookie} from '../js/cache.js'
  export default {
    name: 'home',
    data () {
      return {
        category: [
          {name: 'Trending'},
          {name: 'Movies'},
          {name: 'Lifestyle'},
          {name: 'Breaking news'},
          {name: 'Sports'},
          {name: 'Amusement'},
          {name: 'Technology'},
          {name: 'Science'}
        ],
        data: [],
        showBar: true,
        index2: 0,
        loaded: false
      }
    },
    methods: {
      renderText: function (index, i, k) {
        if (k === 0) {
          var temp = this.data[index * 6 + i - 1].video_src
          var reg = new RegExp('-', 'g')
          return temp.substring(0, temp.length - 4).replace(reg, ' ')
        }
      },
      renderAuthor: function (index, i, k) {
        if (k === 0) {
          return this.data[index * 6 + i - 1].author
        }
      },
      renderViews: function (index, i) {
        var temp
        var n = index * 6 + i - 1
        if (n > 1000000) {
          temp = (this.data[n].views - this.data[n].views % 1000000) / 1000000
          temp += 'M views'
        } else {
          temp = (this.data[n].views - this.data[n].views % 1000) / 1000
          temp += 'K views'
        }
        return temp
      },
      renderTime: function (index, i) {
        var temp = ''
        var random = Math.random()
        if (random > 0.75) {
          random = Math.random() * 100 + 6 * index + i
          temp = Math.round(random % 5)
          if (temp === 0) {
            temp++
          }
          temp += ' Years ago'
        } else if (random > 0.5) {
          random = Math.random() * 100 + 6 * index + i
          temp = Math.round(random % 12)
          if (temp === 0) {
            temp++
          }
          temp += ' months ago'
        } else if (random > 0.25) {
          random = Math.random() * 100 + 6 * index + i
          temp = Math.round(random % 30)
          if (temp === 0) {
            temp++
          }
          temp += ' days ago'
        } else {
          random = Math.random() * 100 + 6 * index + i
          temp = Math.round(random % 12)
          if (temp === 0) {
            temp++
          }
          temp += ' hours ago'
        }
        return temp
      },
      watchLater: function () {
        alert('watch later')
      },
      video: function (a, b) {
        this.$root.src = this.data[a * 6 + b - 1].video_src
        this.$root.eventHub.$emit('loading')
        this.$root.eventHub.$emit('resetIndex2')
        this.$root.eventHub.$emit('user')
        this.$router.push({path: '/video'})
      },
      video2: function (a) {
        this.$root.src = this.data[a - 1].video_src
        this.$router.push({path: '/video'})
      },
      picture: function (a, b) {
        var temp = a * 6 + b - 1
        var str
        var str2 = 'background-size: cover;'
        console.log(this.data[temp].image_src)
        str = 'background: url(\'static/image/' + this.data[temp].image_src + '\') 50%;'
        return str + str2
      },
      home: function () {
        this.$router.push({path: '/home'})
      },
      history: function () {
        if (checkCookie(2, '')) {
          this.$router.push({path: '/history'})
          this.$root.eventHub.$emit('full', true)
        } else {
          this.$root.eventHub.$emit('prompt')
        }
      },
      fixedNav: function (i) {
        if (i === this.index2) {
          return true
        } else {
          return false
        }
      },
      animate: function (a, b) {
        var temp = a * 6 + b - 1
        $('.picture').eq(temp).css('background-image', 'url(\'static/gif/' + this.data[temp].gif_src + '\')')
      },
      cancelAnimate: function (a, b) {
        var temp = a * 6 + b - 1
        $('.picture').eq(temp).css('background-image', 'url(\'static/image/' + this.data[temp].image_src + '\')')
      }
    },
    beforeCreate () {
      var self = this
      $.getJSON('homepage.php', function (data) {
        self.data = data
        console.log(self.data)
      })
    },
    mounted () {
      var self = this
      this.$root.eventHub.$on('bar', function () {
        self.showBar = !self.showBar
      })
      if (!this.$root.adapt) {
        this.$root.eventHub.$emit('nav', 0)
        this.showBar = false
      }
      self.$root.eventHub.$emit('loading')
      setTimeout(function () {
        self.loaded = true
        self.$root.eventHub.$emit('load')
      }, 1000)
    }
  }
</script>

<style scoped>
  #home{
    width: 100%;
    height: auto;
    overflow-x: hidden;
  }

  @media (max-device-width: 767px) {
    .home-content{
      padding: 0;
    }
    .mobile-content{
      width: 100%;
      height: auto;
    }
    .mobile-content>div{
      width: calc(100% - 72px);
      height: 740px;
      overflow: hidden;
      margin: 0 36px;
    }
    .mobile-content>div:not(:last-of-type){
      border-bottom: 3px solid #f0f2f5;
    }
    .video-pic{
      width: 100%;
      height: 510px;
      margin-top: 30px;
    }
    .video-blogger{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      margin: 25px 25px 75px 0;
      float: left;
    }
    .video-pic, .video-blogger{
      background-size: cover!important;
      background-position: 50%!important;
    }
    .video-intro{
      width: calc(100% - 185px);
      height: 200px;
      float: left;
      overflow: hidden;
    }
    .video-intro-title{
      font-size: 36px;
      line-height: 140%;
      margin-top: 25px;
    }
    .video-intro-blogger, .video-intro-views, .video-intro-time{
      float: left;
      font-size: 32px;
      margin: 10px 5px;
    }
    .video-intro-blogger:after, .video-intro-views:after{
      content: " ·";
    }
    .watch-later{
      width: 60px;
      height: 125px;
      background: url("../../static/assets/setting.png") 50% 36px no-repeat;
      background-size: 80%;
      float: left;
    }
    ::-webkit-scrollbar{
      display: none;
    }
  }

  @media (min-device-width: 768px) {
    #category{
      position: fixed;
      display: none;
    }
    .category-item{
      width: calc(100% - 48px);
      height: 40px;
      padding: 1px 24px 0;
      overflow: hidden;
      cursor: pointer;
    }
    #category .category-item:hover{
      background: #EBEBEB;
    }
    .category-item:first-of-type{
      margin-top: 10px;
    }
    .category-item:nth-of-type(2), .category-item:nth-of-type(4){
      margin-top: 0;
      margin-bottom: 0;
    }
    .category-item:nth-of-type(3), .category-item:nth-of-type(6){
      margin-bottom: 10px;
    }
    .category-item svg{
      width: 24px!important;
      height: 24px!important;
      fill: #888888;
      padding: 8px 26px 8px 0;
      float: left;
    }
    .category-item.active svg{
      fill: #1296db;
    }
    .category-item p{
      font-size: 15px;
      line-height: 40px;
      color: #444444;
      float: left;
    }
    .part{
      width: calc(100% - 48px);
      height: 24px;
      display: block;
      padding: 12px 24px 4px;
      border-top: 1px solid #EEEEEE;
      font-size: 14px;
      line-height: 24px;
      color: #8C8C8C;
    }
    .sub{
      width: 24px;
      height: 24px;
      border-radius: 12px;
      margin: 8px 26px 8px 0;
      background-position: 50%!important;
      background-size: cover!important;
      float: left;
    }
    #category-content>p:last-of-type{
      font-size: 14px;
      color: #8C8C8C;
      width: calc(100% - 48px);
      padding: 0 24px;
    }
    #home{
      margin: 58px 0 0;
      background: #FCFCFC;
    }
    .home-content{
      width: 100%;
      height: auto;
      padding: 20px 0;
    }
    .category{
      height: 300px;
      width: 428px;
      margin: 0 calc((100% - 428px) / 2);
      overflow: hidden;
    }
    .category:first-of-type{
      height: 280px;
    }
    .category:not(:last-of-type){
      border-bottom: 1px solid #f0f2f5;
    }
    .category:first-of-type .catName{
      font-size: 18px;
      padding: 10px 0 20px;
    }
    .category:not(:first-of-type) .catName{
      font-size: 18px;
      padding: 35px 0 20px;
    }
    .home-content-wrapper>div{
      height: 240px;
      width: 211px;
      margin-right: 3px;
      float: left;
      overflow: hidden;
      cursor: pointer;
    }
    .home-content-wrapper>div .picture{
      width: 100%;
      height: 120px;
    }
    .home-content-wrapper>div p:first-of-type{
      width: 100%;
      height: auto;
      max-height: 40px;
      font-size: 15px;
      line-height: 20px;
      overflow: hidden;
      margin: 6px 0 0;
    }
    .home-content-wrapper>div p:nth-of-type(2), .home-content-wrapper>div p:nth-of-type(3), .home-content-wrapper>div p:nth-of-type(4){
      font-size: 14px;
      margin-top: 5px;
    }
    .home-content-wrapper>div p:nth-of-type(3), .home-content-wrapper>div p:nth-of-type(4){
      float: left;
      margin-right: 12px;
      margin-top: 5px;
    }
    #category::-webkit-scrollbar{
      width: 8px;
      display: none;
    }
    #category:hover::-webkit-scrollbar{
      display: inline;
    }
    #category::-webkit-scrollbar-thumb{
      border-radius: 4px;
      background: #C8C8C8;
    }
    #category::-webkit-scrollbar-track{
      border-radius: 5px;
      background: transparent;
    }
    #category-content{
      width: 100%;
      height: 1000px;
    }
  }

  @media (min-device-width: 768px) and (min-width: 663px) {
    .category{
      width: 642px;
      margin: 0 calc((100% - 642px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 876px) {
    .category{
      width: 856px;
      margin: 0 calc((100% - 856px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1090px) {
    .category{
      width: 1070px;
      margin: 0 calc((100% - 1070px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1294px) {
    #category{
      display: inline;
      width: 240px;
      height: calc(100% - 58px);
      background: #F5F5F5;
      overflow-y: scroll;
    }
    .showContent{
      width: calc(100% - 240px);
      margin-left: 240px;
    }
    .category{
      width: 1070px;
      margin: 0 calc((100% - 1070px) / 2);
    }
    .category.showCategory{
      width: 856px;
      margin: 0 calc((100% - 856px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1390px){
    .category{
      width: 1284px;
      margin: 0 calc((100% - 1284px) / 2);
    }
    .category.showCategory{
      width: 1070px;
      margin: 0 calc((100% - 1070px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1604px) {
    .category.showCategory{
      width: 1284px;
      margin: 0 calc((100% - 1284px) / 2);
    }
  }
</style>



