<script>
  import $ from 'jquery'
  export default {
    name: 'video-page',
    data () {
      return {
        playStatus: true,
        showBar: true,
        time: 0,
        timer: '',
        prompt: false,
        setVolume: false,
        loaded: false,
        remarkNumber: '47 Comments',
        duration: 0,
        commentData: []
      }
    },
    methods: {
      play: function () {
        this.playStatus = !this.playStatus
        if (!this.playStatus) {
          $('.videoBox').trigger('pause')
        } else {
          $('.videoBox').trigger('play')
        }
      },
      pause: function () {
        clearInterval(this.timer)
      },
      player: function () {
        this.prompt = true
        $('#play').click()
        var self = this
        setTimeout(function () {
          self.prompt = false
        }, 300)
      },
      playing: function () {
        var self = this
        var e = event || window.event
        this.timer = setInterval(function () {
          self.time = e.target.currentTime
          var translate = 100 - ((e.target.currentTime / self.duration) * 100).toFixed(2)
          $('.progress-bar').css('transform', 'translateX(-' + translate + '%)')
        }, 68)
      },
      set: function () {
        var e = event || window.event
        var time
        var width = window.innerWidth
        if (width >= 1760) {
          time = Math.round((e.clientX - this.getOffsetLeft(e.target)) / 1280 * this.duration)
        } else if (width >= 1308) {
          time = Math.round((e.clientX - this.getOffsetLeft(e.target)) / 854 * this.duration)
        } else if (width >= 1094) {
          time = Math.round((e.clientX - this.getOffsetLeft(e.target)) / 640 * this.duration)
        } else if (width >= 878) {
          time = Math.round((e.clientX - this.getOffsetLeft(e.target)) / 854 * this.duration)
        } else if (width >= 664) {
          time = Math.round((e.clientX - this.getOffsetLeft(e.target)) / 640 * this.duration)
        } else {
          time = Math.round((e.clientX - this.getOffsetLeft(e.target)) / 426 * this.duration)
        }
        document.getElementById('videoBox').currentTime = time
      },
      getOffsetLeft: function (obj) {
        var temp = obj.offsetLeft
        var parent = obj.offsetParent
        while (parent !== null) {
          temp += parent.offsetLeft
          parent = parent.offsetParent
        }
        return temp
      },
      random: function (i) {
        var str = 'background: url(\'static/youtube_image/' + i + '.jpg\');'
        var str2 = 'background-size: cover;'
        return str + str2
      },
      control: function () {
        this.showBar = true
        var self = this
        setTimeout(function () {
          if (self.playStatus) {
            self.showBar = false
            self.setVolume = false
          }
        }, 6000)
      },
      hide: function () {
        if (this.playStatus) {
          this.showBar = false
        }
      },
      video: function () {
        this.$router.push({path: '/video'})
        this.$root.eventHub.$emit('user')
      },
      volume: function () {
        this.setVolume = true
      },
      setVol: function () {
        document.getElementById('videoBox').volume = event.target.value / 100
      },
      fullview: function () {
        if (document.getElementById('videoBox').requestFullscreen) {
          document.getElementById('videoBox').requestFullscreen()
        } else if (document.getElementById('videoBox').mozRequestFullScreen) {
          document.getElementById('videoBox').mozRequestFullScreen()
        } else if (document.getElementById('videoBox').msRequestFullscreen) {
          document.getElementById('videoBox').msRequestFullscreen()
        } else if (document.getElementById('videoBox').webkitRequestFullscreen) {
          document.getElementById('videoBox').webkitRequestFullScreen()
        }
      }
    },
    computed: {
      progress: function () {
        var min = Math.round((this.time - this.time % 60) / 60)
        var sec = Math.round(this.time % 60)
        if (sec < 10) {
          sec = '0' + sec
        } else if (sec === 60) {
          min++
          sec = '0' + 0
        }
        var str = min + ':' + sec + '/3:07'
        return str
      },
      sourceRender: function () {
        if (this.$root.src === '') {
          this.$root.src = '更美好的全联接世界.mp4'
        }
        return 'static/video/' + this.$root.src
      },
      renderTitle: function () {
        var reg = new RegExp('-', 'g')
        var temp = this.$root.src
        temp = temp.substring(0, temp.length - 4).replace(reg, ' ')
        return temp
      },
      calcTime: function () {
        var str = ''
        var temp = Math.round(this.duration)
        if (temp > 60) {
          str += (temp - temp % 60) / 60
        } else {
          str = '00'
        }
        str += ':'
        if (temp % 60 < 10) {
          if (temp % 60 === 0) {
            temp = 1
          } else {
            str += '0' + temp
          }
        } else {
          str += temp % 60
        }
        console.log(str)
        return str
      }
    },
    beforeCreate () {
      var self = this
      $.ajax({
        url: 'video_details.php',
        type: 'POST',
        data: {video_src: self.$root.src},
        success: function (data) {
          var outer = data.split('@')
          for (var i = 0; i < outer.length; i++) {
            var inner = outer[i].split('*')
            self.commentData.push({replies: inner[0], user: inner[1], time: inner[2], content: inner[3], likes: inner[4]})
          }
          self.remarkNumber = self.commentData.length + ' Comments'
        }
      })
    },
    mounted () {
      this.control()
      var self = this
      this.$root.eventHub.$on('pause', function () {
        self.play()
      })
      self.$root.eventHub.$emit('loading')
      setTimeout(function () {
        self.loaded = true
        self.$root.eventHub.$emit('load')
        self.duration = 300
        // self.duration = document.getElementById('videoBox').duration
        // console.log(self.duration)
        $.ajax({
          url: 'history.php',
          type: 'POST',
          data: {video_src: self.$root.src},
          success: function () {
            // self.loaded = true
            // self.$root.eventHub.$emit('load')
          }
        })
      }, 1000)
    }
  }
</script>

