import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import hot from '@/components/hot'
import category from '@/components/category'
import sign from '@/components/sign'
import video from '@/components/video'
import user from '@/components/center'
import history from '@/components/history'
import creator from '@/components/creator'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/hot',
      name: 'hot',
      component: hot
    },
    {
      path: '/category',
      name: 'category',
      component: category
    },
    {
      path: '/sign',
      name: 'sign',
      component: sign
    },
    {
      path: '/video',
      name: 'video',
      component: video
    },
    {
      path: '/user',
      name: 'user',
      component: user
    },
    {
      path: '/history',
      name: 'history',
      component: history
    },
    {
      path: '/creator',
      name: 'creator',
      component: creator
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



