<template>
  <div id="creator">
    <div class="createBox">
      <div class="creator-bar">
        <div v-for="(item, index) in funcList" :style="renderDiv(index)">
          <p :style="renderText(index)">{{item}}</p>
        </div>
      </div>
      <div class="channel-bar">
        <div class="user"></div>
        <p>DEAN YE</p>
        <div class="view">
          <p>VIEW CHANNEL</p>
        </div>
      </div>
      <div class="uploadBox">
        <div class="uploadCircle" @click="upload"></div>
        <form action="upload_file.php" method="post" enctype="multipart/form-data">
          <input type="file" name="file" id="file"><br>
          <div class="uploadMode">
            <p>which frame do you prefer to set as cover image</p>
            <input type="input" name="image_point" id="image_point">
          </div>
          <div class="uploadMode">
            <p>which frames do you prefer to set as animated gif</p>
            <input type="input" name="gif_point" id="src_point">
          </div>
          <input type="submit" name="submit" value="提交" id="upload">
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'creator',
    data () {
      return {
        funcList: [
          'CREATOR STUDIO',
          'DASHBOARD',
          'VIDEO MANAGER',
          'LIVE STREAMING',
          'COMMUNITY',
          'CHANNEL',
          'ANALYTICS',
          'TRANSLATION',
          'CREATE',
          'YOUR CONTRIBUTIONS',
          'HELP AND FEEDBACK'
        ]
      }
    },
    methods: {
      renderDiv: function (i) {
        if (i === 0) {
          return 'height: 50px;'
        } else {
          return 'height: 48px;'
        }
      },
      renderText: function (i) {
        if (i === 0) {
          return 'font-weight: bold; padding-top: 24px;'
        } else {
          return 'line-height: 48px;'
        }
      },
      upload: function () {
        $('#file').click()
      }
    },
    mounted () {
      var self = this
      self.$root.eventHub.$emit('loading')
      setTimeout(function () {
        self.loaded = true
        self.$root.eventHub.$emit('load')
      }, 1000)
    }
  }
</script>

<style scoped>
  #creator{
    width: 100%;
    height: auto;
    position: absolute;
    overflow-x: hidden;
    background: #F1F1F1;
  }

  @media (min-device-width: 768px) {
    #creator{
      min-height: calc(100% - 82px);
      padding: 12px 0;
      top: 58px;
    }
    .createBox{
      width: calc(100% - 36px);
      height: auto;
      overflow: hidden;
      margin: 0 18px;
    }
    .creator-bar{
      width: 215px;
      height: 560px;
      background: white;
      float: left;
      overflow: hidden;
    }
    .channel-bar{
      width: calc(100% - 230px);
      height: 90px;
      float: right;
      background: white;
    }
    .creator-bar>div{
      width: calc(100% - 40px);
      padding: 0 20px 1px;
    }
    .creator-bar>div:not(:first-of-type){
      cursor: pointer;
    }
    .creator-bar>div:not(:first-of-type):hover{
      background: #F6F6F6;
    }
    .creator-bar>div p{
      font-size: 14px;
    }
    .channel-bar .user{
      width: 54px;
      height: 54px;
      background: url("../../static/assets/user-circle3.jpg");
      background-size: cover;
      margin: 18px 22px;
      float: left;
    }
    .channel-bar p:first-of-type{
      float: left;
      font-size: 18px;
      padding: 22px 0 0;
    }
    .channel-bar .view{
      width: auto;
      height: auto;
      position: relative;
      top: 30px;
      right: 79px;
      cursor: pointer;
    }
    .channel-bar .view p{
      font-size: 12px;
      color: #4C7ACC;
    }
    .channel-bar .view:hover p{
      text-decoration: underline;
    }
    .uploadBox{
      width: calc(100% - 230px);
      height: 452px;
      margin-top: 16px;
      float: right;
      background: white;
    }
    .uploadCircle{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background: url("../../static/assets/upload2.png");
      background-size: cover;
      margin: 80px calc((100% - 100px) / 2) 20px;
      cursor: pointer;
    }
    .uploadCircle:active{
      -webkit-transform: scale(0.97);
      -moz-transform: scale(0.97);
      -o-transform: scale(0.97);
      transform: scale(0.97);
    }
    #file{
      visibility: hidden;
    }
    .uploadMode{
      width: 100%;
      height: 72px;
      text-align: center;
    }
    .uploadMode p{
      font-size: 15px;
    }
    .uploadMode input{
      width: 100px;
      height: 30px;
      border: none;
      outline: none;
      border-bottom: 1px solid #bfbfbf;
      text-align: center;
      font-size: 16px;
    }
    #upload{
      width: 150px;
      height: 36px;
      margin: 0 calc((100% - 150px) / 2);
      border-radius: 3px;
      border: none;
      outline: none;
      background: #1296db;
      color: white;
      font-size: 16px;
    }
    #upload:active{
      box-shadow: 0 3px 12px grey;
      background: #1E90FF;
    }
  }

  @media (min-device-width: 768px) and (min-width: 694px) {
    .createBox{
      width: 640px;
      margin: 0 calc((100% - 640px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 878px) {
    .createBox{
      width: 854px;
      margin: 0 calc((100% - 854px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1094px) {
    .createBox{
      width: 1066px;
      margin: 0 calc((100% - 1066px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1308px) {
    .createBox{
      width: 1280px;
      margin: 0 calc((100% - 1280px) / 2);
    }
  }

  @media (min-device-width: 768px) and (min-width: 1760px) {
    .createBox{
      width: 1706px;
      margin: 0 calc((100% - 1706px) / 2);
    }
  }
</style>



